<template>
    <b-container id="app" fluid>
        <the-section-storage
            :storage="storage"
            @newData="setStorage" />
        <the-section-overview
            :elements="storage.elements"
            :hierarchy="storage.hierarchy"
            :consumeRelations="selectedConsumeRelations"
            :dependencyRelations="selectedDependencyRelations"
            @selectionChanged="changedSelectedElementIds"
            @changeElement="setElementValue"
            @changeHierarchy="changeHierarchy" />
        <the-section-details
            :relations="selectedRelations"
            :elements="storageObjects"
            :hierarchy="storage.hierarchy"
            :added-relation-id="newRelationId"
            :alert-message="alertMessageDetails"
            @changeRelation="setRelationValue"
            @newRelation="addRelation"
            @alertClose="resetDetailAlert"
            @deleteRelation="deleteRelation"
            @changeHierarchy="changeHierarchy" />
        <b-row></b-row>
    </b-container>
</template>

<script>
import TheSectionOverview from './components/TheSectionOverview.vue'
import TheSectionDetails from './components/TheSectionDetails.vue'
import TheSectionStorage from './components/TheSectionStorage.vue'
import methodsRelation from './js/methodsRelation.js'
import modelElement from './js/modelElement.js'
import modelRelation from './js/modelRelation.js'

export default {
    name: 'App',

    components: {
        TheSectionOverview,
        TheSectionDetails,
        TheSectionStorage
    },

    data() {
        return {
            storage: {
                elements: [],
                hierarchy: [],
                relations: []
            },
            selectedElementIds: [],
            selectedNestedIds: [],
            selectedRelations: [],
            newRelationId: "",
            alertMessageDetails: ""
        }
    },

    computed: {
        storageObjects() {
            return this.storage.elements;
        },

        selectedConsumeRelations() {
            var consumerRelations = [];

            if(this.selectedElementIds.length == 1) {
                var selectedElementIdValues = this.selectedNestedIds.map(r => r.value);
                consumerRelations = this.storage.relations.filter(rel => selectedElementIdValues.indexOf(rel.startId) != -1);
            };
            return consumerRelations;
        },

        selectedDependencyRelations() {
            var dependencyRelations = [];
        
            if(this.selectedElementIds.length < 2) {
                var selectedElementIdValues = this.selectedNestedIds.map(r => r.value);
                dependencyRelations = this.storage.relations.filter(rel => selectedElementIdValues.indexOf(rel.endId) != -1);

            } else if(this.selectedNestedIds.length > 1) {
                var selectedElementIdValues = this.selectedNestedIds.map(r => r.value);
                dependencyRelations = this.storage.relations.filter(rel => {
                    var primaryIndex = selectedElementIdValues.indexOf(rel.endId);
                    var secondaryIndex = selectedElementIdValues.indexOf(rel.startId);

                    if(primaryIndex != -1 && secondaryIndex != -1) {
                        if(this.selectedNestedIds[primaryIndex].parent != this.selectedNestedIds[secondaryIndex].parent) {
                            return true;
                        } else {
                            return false;
                        }
                    };
                    return false;
                });        
            };
            return dependencyRelations;
        },
    },

    watch: {
        selectedConsumeRelations: {
            deep: true,
            handler() {
                this.setSelectionRelations();
            }
        }, 

        selectedDependencyRelations: {
            deep: true,
            handler() {
                this.setSelectionRelations();
            }
        }
    },

    methods: {
        setStorage(storageItems) {
            this.storage = storageItems;
        },

        changedSelectedElementIds(selectedNestedIds, selectedIds) {
            this.selectedNestedIds = selectedNestedIds;

            this.selectedElementIds = selectedIds;
            if(this.selectedElementIds.length == 0) {
                this.selectedNestedIds = this.storage.elements.map(e => ({value: e.id}));
            } 
        },

        setSelectionRelations() {
            var relations = this.selectedConsumeRelations.concat(this.selectedDependencyRelations);
            this.selectedRelations = relations.filter((rel, ind, _arr) => _arr.map(r => r.id).indexOf(rel.id) === ind);
        },

        deleteRelation(relationId) {
            this.storage.relations = this.storage.relations.filter(r => r.id != relationId); 
        },

        setRelationValue(changeObject) {
            var changedRelation = this.storage.relations.find(r => r.id == changeObject.relationId);
            var relationObject = modelRelation.getRelation(changedRelation);

            if(relationObject.isRequirement) { // requirement || realization
                var realizationObject = modelRelation.getRequirmentRealization(changedRelation);

                if(realizationObject[changeObject.prop] != undefined) { // realization
                    modelRelation.setRelationValue(changedRelation, changeObject.prop, changeObject.newValue, this.storage.elements);

                } else {
                    var changedRequirement = this.storage.elements.find(e => e.id == realizationObject.requirement);
                    var requirementObject = modelElement.getRequirement(changedRequirement);
                    
                    if(requirementObject[changeObject.prop] != undefined) { // requirement
                        modelElement.setElementValue(changedRequirement, changeObject.prop, changeObject.newValue, this.storage.relations);
                    };
                };

            } else { // relation
                modelRelation.setRelationValue(changedRelation, changeObject.prop, changeObject.newValue);
            };
        },

        setElementValue(changeObject) {
            var changedElement = this.storage.elements.find(e => e.id == changeObject.elementId);
            
            if(['functions'].indexOf(changeObject.prop) != -1) { // only 'Serviceinstanz'
                var changedFunction = this.storage.elements.find(e => e.id == changeObject.newValue);
                    
                if(changedFunction == undefined) {
                    var newFunctionId = modelElement.addElement(this.storage.elements, changeObject.newValue, 'Servicefunction');
                    changedFunction = this.storage.elements.find(e => e.id == newFunctionId);
                };

                modelElement.setElementValueArray(changedElement, changeObject.prop, changedFunction.id, changeObject.method);

            } else {
                modelElement.setElementValue(changedElement, changeObject.prop, changeObject.newValue);
            };
        },

        changeHierarchy(changeObject) {
            if(changeObject.method == 'delete') {
                var newHierarchy = this.storage.hierarchy.filter(h => !(h.elemid == changeObject.elemid && h.subelemid == changeObject.subelemid));
                if(this.storage.hierarchy.length > newHierarchy.length) {
                    this.storage.hierarchy = newHierarchy;
                }
            } else if(changeObject.method == 'link') {
                var childLink = {
                    elemid: changeObject.elemid,
                    subelemid: changeObject.subelemid
                };
                this.storage.hierarchy.push(childLink);

            } else if(changeObject.method == 'add') {
                var newElementId = this.getRandomElementId();

                if(typeof(changeObject.subelemid) == 'object') {
                    var newElementId = modelElement.addElement(elementStorage, changeObject.subelemid.name, changeObject.subelemid.type);

                    var childLink = {
                        elemid: changeObject.elemid,
                        subelemid: newElementId
                    };

                } else if(typeof(changeObject.elemid) == 'object') {
                    var newElementId = modelElement.addElement(elementStorage, changeObject.elemid.name, changeObject.elemid.type);

                    var childLink = {
                        elemid: newElementId,
                        subelemid: changeObject.subelemid
                    };
                }
                
                this.storage.hierarchy.push(childLink);
            };
            

            // @ignore timestamp?
        },

        resetDetailAlert() {
            this.alertMessageDetails = "";
        },

        addRelation(changeObject) {
            var isSetRequirementId = (changeObject.element != undefined);
            if(!isSetRequirementId && this.selectedElementIds.length == 0) {
                this.alertMessageDetails = "Min. ein Element selektieren";

            } else {
                var startElement = this.storage.elements.find(e => e.id == this.selectedElementIds[0]);
                var newRelationId = modelRelation.addRelation(this.storage.relations,
                    (startElement != undefined ? this.selectedElementIds[0] : ""),
                    (this.selectedElementIds[1] != undefined ? this.selectedElementIds[1] : ""),
                    (startElement != undefined ? startElement.type : ""),
                );

                var relationObject = this.storage.relations.find(rel => rel.id == newRelationId);
                modelRelation.setRelationValue(relationObject, 'status', this.$relationStatus[0]);

                if(changeObject.type == 'Requirement') {
                    if(!isSetRequirementId) {
                        var linkedRequirementId = modelElement.addElement(
                            this.storage.elements, 
                            "Neues Requirement", 
                            "Requirement",
                            [{ name: "commitment", value: this.$commitment[0] }]
                        );
                    } else { 
                        var linkedRequirementId = changeObject.element;
                    };

                    modelRelation.setRelationValue(relationObject, 'element', linkedRequirementId);
                    var requirementElement = this.storage.elements.find(e => e.id == linkedRequirementId);
                    if(startElement != undefined) {
                        modelElement.setElementValue(requirementElement, 'source', startElement.id, this.storage.relations);
                    };
                    if(changeObject.startId != undefined) {
                        modelElement.setElementValue(requirementElement, 'source', changeObject.startId, this.storage.relations);
                    };
                    if(changeObject.endId != undefined) {
                      modelRelation.setRelationValue(relationObject, 'endId', changeObject.endId, this.storage.elements);
                    };
                };

                this.newRelationId = newRelationId;
            };
        },
    }
}
</script>

<style>

</style>


