<template>
    <div class="row mt-2">
        <div class="col-2">{{ rowLabel }}:</div>
        <div class="col-10"><b-form-select v-model="dataObject[objectProp]" :options="propValues" @change="itemChanged(dataObject[objectProp], objectProp)" :disabled="!isEditable"></b-form-select></div>
    </div>
</template>

<script>
export default {
    props: {
        rowLabel: {
            type: String,
            required: true
        },

        dataObject: {
            type: Object,
            required: true
        },

        objectProp: {
            type: String,
            required: true
        },

        propValues: {
            type: Array,
            required: true
        },

        isEditable: {
            type: Boolean,
            required: false,
            default: true
        }
    },

    methods: {
        itemChanged(newVal, itemProp) {
            this.$emit('propChanged', newVal, itemProp);
        }
    }
}
</script>

<style scoped>

</style>


