<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.requirement-children variant="outline-dark">
                <div>Requirement :: Kinder <b-badge pill :class="{'bg-info': requirementChildren.length > 0 }">{{ (requirementChildren.length > 0 ? requirementChildren.length : "") }}</b-badge></div>
            </b-button>
        </b-card-header>
        <b-collapse id="requirement-children" accordion="modal-accordion" role="tabpanel">
        <b-card-body>
        <b-container fluid>
            <my-modal-row-add-link
                :ddItems="ddRequirements"
                buttonText="Requirement"
                :allowNewValue="false"
                @linkItem="addChild"
            />
            <b-card no-body class="m-1 p-2" v-for="req in requirementChildren" :key="req.id">
                <b-row class="mt-2">
                    <b-col cols="3"><h6><span class="badge bg-primary text-white">{{ req.tablet }}</span></h6></b-col>
                    <b-col cols="8"></b-col>
                    <b-col cols="1"><b-button size="sm" variant="outline-dark" @click="showRelation(req.id)"><b-icon-gear></b-icon-gear></b-button></b-col>
                </b-row>
                <my-modal-row-input
                    rowLabel="Name"
                    :dataObject="req"
                    objectProp="name"
                    :isEditable="false" />
                <my-modal-row-textarea
                    rowLabel="Beschreibung"
                    :dataObject="req"
                    objectProp="desc"
                    :isEditable="false" />
            </b-card>
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import MyModalRowTextarea from './MyModalRowTextarea.vue';
import MyModalRowInput from './MyModalRowInput.vue';
import MyModalRowAddLink from './MyModalRowAddLink.vue'

export default {
    props: {
        requirement: {
            type: Object,
            required: true
        },

        elements: {
            type: Array,
            required: true
        },

        hierarchy: {
            type: Array,
            required: true
        },
    },

    components: {
        MyModalRowTextarea,
        MyModalRowInput,
        MyModalRowAddLink
    },

    computed: {
        ddRequirements() {
            var ddElements = this.elements
                .filter(e => ['Requirement'].indexOf(e.type) != -1)
                .map((elem) => ({ 
                    value: elem.id, 
                    text: elem.name, 
                    tablet: this.getRequirementValue(elem, 'tablet') 
                }));
            var groupedElements = d3.nest()
                .key(function(e) {return e.tablet})
                .entries(ddElements);
            groupedElements = groupedElements.map((elem) => ({
                label: elem.key,
                options: elem.values.sort((a,b) => (a.text < b.text ? -1 : 1))
                })
            );
            return groupedElements;
        },

        requirementChildren() {
            var childrenIds = this.hierarchy.filter(e => e.elemid == this.requirement.id)
                .map(e => e.subelemid);
            return this.elements.filter(e => childrenIds.indexOf(e.id) != -1)
                .map((req) => ({
                    id: req.id,
                    name: req.name,
                    tablet: this.getRequirementValue(req, 'tablet'),
                    desc: this.getRequirementValue(req, 'description'),
                }));

        }
    },

    methods: {
        getRequirementValue(req, valueName) {
            var valueObject = req.values.find(rv => rv.name == valueName);
            if(valueObject != undefined) {
                return valueObject.value;
            }
            return "";
        },

        showRelation(relationId) {
            this.$emit('showRelation', relationId);
        },

        addChild(childId) {
            var changeObject = {
                elemid: this.requirement.id,
                subelemid: childId,
                method: 'link'
            };
            this.$emit('changeHierarchy', changeObject);
        }
    },
}
</script>

<style scoped>
    
</style>


