export default {
    getRelationsByRequirementId(relations, requirementId) {
        return relations.filter((rel) => {
            var valueElement = rel.values.find(rv => rv.name == 'element');
            if (valueElement != undefined) { // requirement
                if (valueElement.value == requirementId) {
                    return true;
                }
            };
            return false;
        });
    }
};


