<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.general-info variant="outline-dark">Allgemein</b-button>
        </b-card-header>
        <b-collapse id="general-info" visible accordion="modal-accordion" role="tabpanel">
        <b-card-body>
        <b-container fluid>
            <my-modal-row-input
                rowLabel="Beziehungs-Id"
                :dataObject="relation"
                objectProp="id"
                :isEditable="false" />
            <my-modal-row-input
                rowLabel="Typ"
                :dataObject="relation"
                objectProp="type"
                :isEditable="false" />
            <my-modal-row-select v-if="!isRequirement"
                rowLabel="Ebene"
                :dataObject="relation"
                objectProp="layer"
                :propValues="ddLayer"
                :isEditable="false" />
            <my-modal-row-select v-if="!isRequirement" 
                rowLabel="Tablet"
                :dataObject="relation"
                objectProp="tablet"
                :propValues="ddTablet"
                @propChanged="itemChanged" />
            <my-modal-row-select
                rowLabel="Start"
                :dataObject="relation"
                objectProp="startId"
                :propValues="ddElements"
                :isEditable="!isRequirement"
                @propChanged="itemChanged" />
            <my-modal-row-select v-if="!isRequirement"
                rowLabel="Ende"
                :dataObject="relation"
                objectProp="endId"
                :propValues="ddElements"
                @propChanged="itemChanged" />
            <my-modal-row-textarea v-if="!isRequirement"
                rowLabel="Titel"
                :dataObject="relation"
                objectProp="title"
                @propChanged="itemChanged" />
            <my-modal-row-textarea v-if="!isRequirement"
                rowLabel="Beschreibung"
                :dataObject="relation"
                objectProp="description"
                @propChanged="itemChanged" />
            <my-modal-row-select v-if="!isRequirement"
                rowLabel="Status"
                :dataObject="relation"
                objectProp="status"
                :propValues="ddStatus"
                @propChanged="itemChanged" />
            <my-modal-row-textarea v-if="!isRequirement"
                rowLabel="Bemerkung"
                :dataObject="relation"
                objectProp="comment"
                @propChanged="itemChanged" />
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import MyModalRowSelect from './MyModalRowSelect.vue'
import MyModalRowInput from './MyModalRowInput.vue'
import MyModalRowTextarea from './MyModalRowTextarea.vue'
import MyModalRowDropdownItem from './MyModalRowDropdownItem.vue'

export default {
    props: {
        relation: {
            type: Object,
            required: true
        },
        elements: {
            type: Array,
            required: true
        }
    },
    
    components: { 
        MyModalRowSelect,
        MyModalRowInput,
        MyModalRowTextarea,
        MyModalRowDropdownItem
    },

    computed: {
        isRequirement() {
            return this.relation.type == 'Requirement';
        },

        ddStatus() {
            if(this.$relationStatus != undefined) {
                return this.$relationStatus;
            };
            return [];
        },

        ddLayer() {
            if(this.$viewLayers != undefined) {
                return this.$viewLayers.map(l => l.name);
            };
            return [];
        },

        ddTablet() {
            if(this.$tabletComponents != undefined) {
                return this.$tabletComponents;
            };
            return [];
        },

        ddElements() {
            return this.elements.filter(e => e.type == this.relation.layer)
                .map((e) => {
                    return {
                        value: e.id,
                        text: e.name
                    }
                }).sort((a,b) => {
                    return (a.text > b.text ? 1 : -1);
                });
        },
    },

    methods: {
        itemChanged(newVal, itemProp) {
            this.$emit('changeItem', newVal, itemProp);
        },
    }
}
</script>

<style scoped>

</style>


