import methodsGeneral from './methodsGeneral.js'
import modelElement from './modelElement.js'

function getRandomRelationId(relationStorage) {
    var randomNumber;
    do {
        randomNumber = Math.floor((Math.random() * 1000000) + 1);
    } while (relationStorage.map(r => r.id).indexOf("CON" + randomNumber) != -1);

    return "CON" + randomNumber;
};

export default {
    getRelation(relationObject) {
        var isRealization = (methodsGeneral.getJSONObjectValueByName(relationObject, 'element').length > 0);

        return {
            id: relationObject.id,
            type: (isRealization ? "Requirement" : "Architektur"),
            isRequirement: isRealization,
            tablet: methodsGeneral.getJSONObjectValueByName(relationObject, 'tablet'),
            startId: relationObject.startId,
            endId: relationObject.endId,
            layer: relationObject.layer,
            title: methodsGeneral.getJSONObjectValueByName(relationObject, 'title'),
            description: methodsGeneral.getJSONObjectValueByName(relationObject, 'description'),
            status: methodsGeneral.getJSONObjectValueByName(relationObject, 'status'),
            comment: methodsGeneral.getJSONObjectValueByName(relationObject, 'comment'),
        }
    },

    getRequirmentRealization(relationObject) {
        return {
            id: relationObject.id,
            type: "Requirement",
            isRequirement: true,
            startId: relationObject.startId,
            endId: relationObject.endId,
            layer: relationObject.layer,
            requirement: methodsGeneral.getJSONObjectValueByName(relationObject, 'element'),
            solved: methodsGeneral.getJSONObjectValueByName(relationObject, 'solved'),
            status: methodsGeneral.getJSONObjectValueByName(relationObject, 'status'),
            comment: methodsGeneral.getJSONObjectValueByName(relationObject, 'comment'),
        }
    },

    setRelationValue(relationObject, relationProp, newValue, storageElements) {
        if (['id', 'startId', 'layer'].indexOf(relationProp) != -1) { // relation prop
            relationObject[relationProp] = newValue;

        } else if (relationProp == 'endId') { // relation multi-set
            relationObject[relationProp] = newValue;

            var elementStorage = storageElements.find(e => e.id == newValue);
            var endObject = modelElement.getElement(elementStorage);
            relationObject['layer'] = endObject.type;

        } else { // relation values
            methodsGeneral.setJSONObjectValueByName(relationObject, relationProp, newValue);
        };

        methodsGeneral.setJSONObjectValueByName(relationObject, 'lastChange', methodsGeneral.getCurrentDate());
    },

    addRelation(relationStorage, relationStartId, relationEndId, relationLayer, relationValues) {
        var newRelationId = getRandomRelationId(relationStorage);
        var relationObject = {
            id: newRelationId,
            startId: relationStartId,
            endId: relationEndId,
            layer: relationLayer
        };

        var relationValueObjects = [];
        if (relationValues != undefined) {
            relationValueObjects = relationValues;
        }
        relationObject.values = relationValueObjects;
        methodsGeneral.setJSONObjectValueByName(relationObject, 'lastChange', methodsGeneral.getCurrentDate());

        relationStorage.push(relationObject);

        return newRelationId;
    }
}


