export default {
    getCurrentDate() {
        var today = new Date(Date.now());
        return today.toISOString().substr(0, 10);
    },

    getJSONObjectValueByName(element, valueName, defaultType) {
        var valueObject = element.values.find(ev => ev.name == valueName);
        if (valueObject != undefined) {
            return valueObject.value;
        }
        if (defaultType == "Array") {
            return [];
        }
        return "";
    },

    setJSONObjectValueByName(element, valueName, newValue) {
        var valueObject = element.values.find(ev => ev.name == valueName);
        if (valueObject != undefined) { // updating
            valueObject.value = newValue;
        } else { // new
            valueObject = {
                name: valueName,
                value: newValue
            };
            element.values.push(valueObject);
        };
    },
}


