<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.requirement-targets variant="outline-dark">
                <div>Requirement :: Realisierung <b-badge pill :class="{'bg-info': realizations.length > 0 }">{{ (realizations.length > 0 ? realizations.length : "") }}</b-badge></div>
            </b-button>
        </b-card-header>
        <b-collapse id="requirement-targets" accordion="modal-accordion" role="tabpanel">
        <b-card-body>
        <b-container fluid>
            <my-modal-row-add-link
                :ddItems="ddLayeredElements"
                buttonText="Relation"
                :allowNewValue="false"
                @linkItem="linkRealization"
            />
            <b-card no-body class="m-1 p-2" v-for="rel in realizations" :key="rel.id">
                <b-row class="mt-2 mb-2" v-if="rel.id != selectedRelationId">
                    <b-col><b-button size="sm" variant="outline-dark" @click="showRelation(rel.id)"><b-icon-gear></b-icon-gear></b-button></b-col>
                </b-row>
                <my-modal-row-dropdown-item
                    rowLabel="Ende"
                    :elements="elements"
                    :selectedItemId="rel.endId"
                    :isEditable="rel.id == selectedRelationId"
                    @changeItem="changeEnd" />
                <my-modal-row-select
                    rowLabel="Status"
                    :dataObject="rel"
                    objectProp="status"
                    :propValues="ddStatus"
                    :isEditable="rel.id == selectedRelationId"
                    @propChanged="itemChanged" />
                <my-modal-row-dropdown-item
                    rowLabel="Realisiert"
                    :elements="elements"
                    :relations="relations"
                    :selectedItemId="rel.solved"
                    :isEditable="rel.id == selectedRelationId"
                    @changeItem="changeSolved" />
                <my-modal-row-textarea
                    rowLabel="Bemerkung"
                    :dataObject="rel"
                    objectProp="comment"
                    :isEditable="rel.id == selectedRelationId"
                    @propChanged="itemChanged" />
            </b-card>
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import MyDropdownItem from './MyDropdownItem.vue'
import MyModalRowAddLink from './MyModalRowAddLink.vue'
import MyModalRowTextarea from './MyModalRowTextarea.vue'
import MyModalRowDropdownItem from './MyModalRowDropdownItem.vue'
import MyModalRowSelect from './MyModalRowSelect.vue'
import methodsRelation from '../js/methodsRelation.js'
import modelRelation from '../js/modelRelation.js'

export default {
    props: {
        selectedRelationId: {
            type: String,
            required: false,
            default: ""
        },
        
        elements: {
            type: Array,
            required: true
        },
        
        relations: {
            type: Array,
            required: true
        },

        requirement: {
            type: Object,
            required: true
        },
    },

    components: {
        MyDropdownItem,
        MyModalRowAddLink,
        MyModalRowTextarea,
        MyModalRowDropdownItem,
        MyModalRowSelect
    },

    computed: {
        ddStatus() {
            if(this.$relationStatus != undefined) {
                return this.$relationStatus;
            };
            return [];
        },

        ddLayeredElements() {
            var ddElements = this.elements
                .filter(e => ['Requirement', 'Applikationsfunktion'].indexOf(e.type) == -1)
                .map((elem) => ({ 
                    value: elem.id, 
                    text: elem.name, 
                    type: elem.type 
                }));
            var groupedElements = d3.nest()
                .key((e) => e.type)
                .entries(ddElements);
            groupedElements = groupedElements.map((elem) => ({
                label: elem.key,
                options: elem.values.sort((a,b) => (a.text < b.text ? -1 : 1))
                })
            );
            return groupedElements;
        },

        realizations() {
            var relations = methodsRelation.getRelationsByRequirementId(this.relations, this.requirement.id);
            return relations.map((rel) => (modelRelation.getRequirmentRealization(rel)));
        }
    },

    methods: {
        linkRealization(endElementId) {
            var changeObject = {};
            changeObject.startId = this.requirement.source;
            changeObject.endId = endElementId;
            changeObject.type = "Requirement";
            changeObject.element = this.requirement.id;

            this.$emit('addRelation', changeObject);
        },

        showRelation(relationId) {
            this.$emit('showRelation', relationId);
        },

        changeEnd(itemId) {
            this.$emit('changeItem', itemId, 'endId');
        },

        changeSolved(itemId) {
            this.$emit('changeItem', itemId, 'solved');
        },

        itemChanged(newVal, itemProp) {
            this.$emit('changeItem', newVal, itemProp);
        }
    }
}
</script>

<style scoped>

</style>


