<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.requirement-info variant="outline-dark">Requirement :: Allgemein</b-button>
        </b-card-header>
        <b-collapse id="requirement-info" accordion="modal-accordion" role="tabpanel">
        <b-card-body>
        <b-container fluid>
            <my-modal-row-input
                rowLabel="Requirement-Id"
                :dataObject="requirement"
                objectProp="id"
                :isEditable="false" />
            <my-modal-row-dropdown-item
                rowLabel="Quelle"
                :elements="elements"
                :selectedItemId="requirement.source"
                @changeItem="changeSource" />
            <my-modal-row-select
                rowLabel="Tablet"
                :dataObject="requirement"
                objectProp="tablet"
                :propValues="ddTablet"
                @propChanged="itemChanged" />
            <my-modal-row-select
                rowLabel="Typ"
                :dataObject="requirement"
                objectProp="type"
                :propValues="ddRequirementTypes"
                @propChanged="itemChanged" />
            <my-modal-row-select
                rowLabel="Verbindlichkeit"
                :dataObject="requirement"
                objectProp="commitment"
                :propValues="ddCommitment"
                @propChanged="itemChanged" />
            <my-modal-row-input
                rowLabel="Quellen-Details"
                :dataObject="requirement"
                objectProp="sourcedetails"
                @propChanged="itemChanged" />
            <my-modal-row-textarea
                rowLabel="Titel"
                :dataObject="requirement"
                objectProp="title"
                @propChanged="itemChanged" />
            <my-modal-row-textarea
                rowLabel="Beschreibung"
                :dataObject="requirement"
                objectProp="description"
                @propChanged="itemChanged" />
            <my-modal-row-input
                rowLabel="Mehrwert"
                :dataObject="requirement"
                objectProp="benefit"
                @propChanged="itemChanged" />
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import MyModalRowInput from './MyModalRowInput.vue';
import MyModalRowSelect from './MyModalRowSelect.vue';
import MyModalRowTextarea from './MyModalRowTextarea.vue';
import MyModalRowDropdownItem from './MyModalRowDropdownItem.vue';

export default {
    props: {
        requirement: {
            type: Object,
            required: true
        },

        elements: {
            type: Array,
            required: true
        }
    },

    components: { 
        MyModalRowInput,
        MyModalRowSelect,
        MyModalRowTextarea,
        MyModalRowDropdownItem
    },

    computed: {
        ddRequirementTypes() {
            if(this.$requirementTypes != undefined) {
                return this.$requirementTypes;
            };
            return [];
        },

        ddCommitment() {
            if(this.$commitment != undefined) {
                return this.$commitment;
            };
            return [];
        },

        ddTablet() {
            if(this.$tabletComponents != undefined) {
                return this.$tabletComponents;
            };
            return [];
        },
    },

    methods: {
        itemChanged(newVal, itemProp) {
            this.$emit('changeItem', newVal, itemProp);
        },

        changeSource(sourceId) {
            this.$emit('changeItem', sourceId, 'source');
        }
    }
}
</script>

<style scoped>

</style>


