<template>
    <b-modal 
        v-model="showComponentModel" 
        scrollable
        size="xl"
        ok-only
        :title="modalTitle"
        @hide="hideModal"
        @blur="hideModal"
        @close="hideModal" >
        <b-container fluid>
            <b-row class="mb-2" v-if="viewHistory.length > 0">
                <b-col><b-button variant="outline-dark" @click="showLastView()"><b-icon-arrow-left></b-icon-arrow-left></b-button></b-col> 
            </b-row>
            <b-row>
                <b-col>
                    <div class="accordion" role="tablist">
                        <my-relation-modal-general v-if="isRelation"
                            :relation="displayedRelation"
                            :elements="elements"
                            @changeItem="itemChanged"
                            />
                        <my-relation-modal-requirement-details v-if="isRequirement"
                            :requirement="displayedRequirement"
                            :elements="elements"
                            @changeItem="itemChanged"
                            />
                        <my-relation-modal-requirement-parent v-if="isRequirement"
                            :requirement="displayedRequirement"
                            :elements="elements"
                            :hierarchy="hierarchy"
                            @showRelation="showRelation"
                            @changeHierarchy="changeHierarchy"
                            />
                        <my-relation-modal-requirement-children v-if="isRequirement"
                            :requirement="displayedRequirement"
                            :elements="elements"
                            :hierarchy="hierarchy"
                            @showRelation="showRelation"
                            @changeHierarchy="changeHierarchy"
                            />
                        <my-relation-modal-requirement-realize v-if="isRequirement"
                            :selectedRelationId="displayedRelationId"
                            :requirement="displayedRequirement"
                            :elements="elements"
                            :relations="relations"
                            @addRelation="addRelation"
                            @showRelation="showRelation"
                            @changeItem="itemChanged"
                            />
                    </div>
                </b-col>
            </b-row>
        </b-container>
    </b-modal>
</template>

<script>
import MyRelationModalRequirementRealize from './MyRelationModalRequirementRealize.vue';
import MyRelationModalRequirementChildren from './MyRelationModalRequirementChildren.vue';
import MyRelationModalRequirementDetails from './MyRelationModalRequirementDetails.vue';
import MyRelationModalGeneral from './MyRelationModalGeneral.vue';
import MyRelationModalRequirementParent from './MyRelationModalRequirementParent.vue';
import modelRelation from '../js/modelRelation';
import modelElement from '../js/modelElement';

export default {
    props: {
        relationItemId: {
            type: String,
            required: false,
            default: ""
        },
        requirementId: {
            type: String,
            required: false,
            default: ""
        },
        show: {
            type: Boolean,
            default: false
        },
        elements: {
            type: Array,
            required: true
        },
        relations: {
            type: Array,
            required: true
        },
        hierarchy: {
            type: Array,
            required: true
        }
    },

    components: { 
        MyRelationModalRequirementChildren,
        MyRelationModalRequirementRealize,
        MyRelationModalRequirementDetails,
        MyRelationModalGeneral,
        MyRelationModalRequirementParent
    },

    data() {
        return {
            displayedRelationId: "",
            displayedRequirementId: "",
            showComponentModel: this.show,
            viewHistory: [],
        }
    },

    computed: {
        displayedRelation() {
            if(this.displayedRelationId.length > 0) {
                var storageRelation = this.relations.find(r => r.id == this.displayedRelationId);
                if(storageRelation != undefined) { // reason: selection change => selectedRelation not more visible
                    var relationElement = modelRelation.getRelation(storageRelation);
                    if(relationElement.isRequirement) {
                        return modelRelation.getRequirmentRealization(storageRelation);
                    };
                    return relationElement;
                }
            };
            return undefined;
        },

        displayedRequirement() {
            if(this.displayedRequirementId.length > 0) {
                var storageElement = this.elements.find(e => e.id == requirementId);
                return modelElement.getRequirement(storageElement);

            } else if(this.displayedRelation != undefined && this.displayedRelation.isRequirement) {                
                var storageElement = this.elements.find(e => e.id == this.displayedRelation.requirement);
                return modelElement.getRequirement(storageElement);
            }
            return undefined;
        },

        isRequirement() {
            return this.displayedRequirement != undefined;
        },

        isRelation() {
            return this.displayedRelation != undefined;
        },

        modalTitle() {
            if(this.isRelation && this.isRequirement) {
                return "Realisierung von " + this.displayedRelation.id + " (Requirement " + this.displayedRequirement.id + ")";
            } else if(this.isRequirement) {
                return "Requirement " + this.displayedRequirement.id;
            } else if(this.isRelation) {
                return "Beziehung " + this.displayedRelation.id;
            };
            return "failed";
        },
    },

    watch: {
        show(newVal) {
            this.showComponentModel = newVal;
            if(newVal) {
                this.displayedRelationId = this.relationItemId;
                this.displayedRequirementId = this.requirementId;
            }
        },

        relationItemId(newVal) {
            this.displayedRelationId = newVal;
        },

        requirementId(newVal) {
            this.displayedRequirementId = newVal;
        }
    },
 
    methods: {
        itemChanged(newVal, itemProp) {
            var changeObject = {};
            changeObject.relationId = this.displayedRelation.id;
            changeObject.prop = itemProp;
            changeObject.newValue = newVal;
            this.$emit('recordChange', changeObject);
        },

        hideModal() {
            this.viewHistory = [];
            this.$emit('closeModal', false);
        },

        addRelation(changeObject) {
            if(changeObject.type == undefined) {
                changeObject.type = "Relation";
            }
            this.$emit('newRelation', changeObject);
        },

        showLastView() {
            var showRelationId = this.viewHistory.pop();
            this.$emit('showRelationDetails', showRelationId);
        },

        showRelation(relationId) {
            this.viewHistory.push(this.displayedRelation.id);
            this.$emit('showRelationDetails', relationId);
        },

        changeHierarchy(changeObject) {
            this.$emit('changeHierarchy', changeObject);
        }
    }
}
</script>

<style scoped>

</style>


