<template>
    <div class="row mt-2">
        <div class="col-2">{{ rowLabel }}:</div>
        <div class="col-10">
            <my-dropdown-item
                :elements="elements"
                :relations="relations"
                :selectedItemId="selectedItemId"
                :isEditable="isEditable"
                @changeItem="changeItem"
            />
        </div>
    </div>
</template>

<script>
import MyDropdownItem from "./MyDropdownItem.vue"

export default {
    props: {
        rowLabel: {
            type: String,
            required: true
        },

        elements: {
            type: Array,
            required: false,
            default: () => []
        },

        relations: {
            type: Array,
            required: false,
            default: () => []
        },

        selectedItemId: {
            type: String,
            required: true
        },
        
        isEditable: {
            type: Boolean,
            required: false,
            default: true
        },
    },

    components: {
        MyDropdownItem
    },

    methods: {
        changeItem(itemId) {
            this.$emit('changeItem', itemId);
        }
    }
}
</script>

<style scoped>

</style>


