<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.requirement-parent variant="outline-dark">
                <span>Requirement :: Eltern <b-badge pill :class="{'bg-info': (requirementParent != undefined) }">{{ (requirementParent != undefined ? "set" : "") }}</b-badge></span>
            </b-button>
        </b-card-header>
        <b-collapse id="requirement-parent" accordion="modal-accordion" role="tabpanel">
        <b-card-body>
        <b-container fluid>
            <b-row>
                <b-col>
                    <my-modal-row-add-link v-if="requirementParent == undefined"
                        :ddItems="ddRequirements"
                        buttonText="Requirement"
                        :allowNewValue="false"
                        @linkItem="addParent"
                    />
                </b-col>
            </b-row>
            <b-row v-if="requirementParent != undefined">
                <b-col cols="11">
                    <b-card no-body class="m-1 p-2">
                        <b-row class="mt-2">
                            <b-col>
                                <h6><span class="badge bg-primary text-white">{{ requirementParent.tablet }}</span></h6>
                            </b-col>
                        </b-row>
                        <my-modal-row-input
                            rowLabel="Name"
                            :dataObject="requirementParent"
                            objectProp="name"
                            :isEditable="false" />
                        <my-modal-row-textarea
                            rowLabel="Beschreibung"
                            :dataObject="requirementParent"
                            objectProp="desc"
                            :isEditable="false" />
                    </b-card>
                </b-col>
                <b-col cols="1">
                    <b-button class="mt-1" size="sm" variant="outline-dark" @click="showRelation(requirementParent.id)"><b-icon-gear></b-icon-gear></b-button>
                    <my-button-unlink
                        :item-id="requirementParent.id"
                        @unlinkItem="deleteHierarchy" />
                </b-col>
            </b-row>
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import MyModalRowTextarea from './MyModalRowTextarea.vue';
import MyModalRowInput from './MyModalRowInput.vue';
import MyModalRowAddLink from './MyModalRowAddLink.vue';
import MyButtonUnlink from './MyButtonUnlink.vue';

export default {
    props: {
        requirement: {
            type: Object,
            required: true
        },

        elements: {
            type: Array,
            required: true
        },

        hierarchy: {
            type: Array,
            required: true
        },
    },

    components: {
        MyModalRowTextarea,
        MyModalRowInput,
        MyModalRowAddLink,
        MyButtonUnlink
    },

    computed: {
        ddRequirements() {
            var ddElements = this.elements
                .filter(e => ['Requirement'].indexOf(e.type) != -1)
                .map((elem) => ({ 
                    value: elem.id, 
                    text: elem.name, 
                    tablet: this.getRequirementValue(elem, 'tablet') 
                }));
            var groupedElements = d3.nest()
                .key(function(e) {return e.tablet})
                .entries(ddElements);
            groupedElements = groupedElements.map((elem) => ({
                label: elem.key,
                options: elem.values.sort((a,b) => (a.text < b.text ? -1 : 1))
                })
            );
            return groupedElements;
        },

        requirementParent() {
            var parentHierarchy = this.hierarchy.find(e => e.subelemid == this.requirement.id);
            if(parentHierarchy != undefined) {
                var parentElement = this.elements.find(e => e.id == parentHierarchy.elemid);
                if(parentElement != undefined) {
                    return {
                        id: parentElement.id,
                        name: parentElement.name,
                        tablet: this.getRequirementValue(parentElement, 'tablet'),
                        desc: this.getRequirementValue(parentElement, 'description'),
                    };
                };
            };
            return undefined;
        }
    },

    methods: {
        getRequirementValue(req, valueName) {
            var valueObject = req.values.find(rv => rv.name == valueName);
            if(valueObject != undefined) {
                return valueObject.value;
            }
            return "";
        },

        showRelation(parentId) {
            this.$emit('showRelation', relationId);
        },

        addParent(parentId) {
            var changeObject = {
                elemid: parentId,
                subelemid: this.requirement.id,
                method: 'link'
            };
            this.$emit('changeHierarchy', changeObject);
        },

        deleteHierarchy(parentId) {
            var changeObject = {
                elemid: parentId,
                subelemid: this.requirement.id,
                method: 'delete'
            };
            this.$emit('changeHierarchy', changeObject);
        }
    },
}
</script>

<style scoped>
    
</style>


