import methodsGeneral from './methodsGeneral.js'
import modelRelation from './modelRelation.js'
import methodsRelation from './methodsRelation.js'

function getRandomElementId(elementStorage) {
    var randomNumber;
    do {
        randomNumber = Math.floor((Math.random() * 1000000) + 1);
    } while (elementStorage.map(r => r.id).indexOf("ELM" + randomNumber) != -1);

    return "ELM" + randomNumber;
};

export default {
    getElement(elementObject) {
        return {
            id: elementObject.id,
            name: elementObject.name,
            type: elementObject.type,
            values: elementObject.values
        }
    },

    // elements: type specific
    getRelease(elementObject) {
        return {
            id: elementObject.id,
            name: elementObject.name,
            type: elementObject.type,
            releaseStart: methodsGeneral.getJSONObjectValueByName(releaseObject, 'releaseStart'),
            releaseEnd: methodsGeneral.getJSONObjectValueByName(releaseObject, 'releaseEnd')
        }
    },

    getRequirement(elementObject) {
        return {
            id: elementObject.id,
            name: elementObject.name,
            type: elementObject.type,
            description: methodsGeneral.getJSONObjectValueByName(elementObject, 'description'),
            tablet: methodsGeneral.getJSONObjectValueByName(elementObject, 'tablet'),
            source: methodsGeneral.getJSONObjectValueByName(elementObject, 'source'),
            sourcedetails: methodsGeneral.getJSONObjectValueByName(elementObject, 'sourcedetails'),
            commitment: methodsGeneral.getJSONObjectValueByName(elementObject, 'commitment'),
            type: methodsGeneral.getJSONObjectValueByName(elementObject, 'type'),
            benefit: methodsGeneral.getJSONObjectValueByName(elementObject, 'benefit'),
        }
    },

    getServiceInstance(elementObject) {
        return {
            id: elementObject.id,
            name: elementObject.name,
            type: elementObject.type,
            functions: methodsGeneral.getJSONObjectValueByName(elementObject, 'functions'),
        }
    },

    setElementValue(elementObject, elementProp, newValue, storageRelations) {
        if (['id', 'name', 'type'].indexOf(elementProp) != -1) { // element prop
            elementObject[elementProp] = newValue;

        } else if (elementProp == 'source' && storageRelations != undefined) { // element value with relation impact
            methodsGeneral.setJSONObjectValueByName(elementObject, elementProp, newValue);

            var requirementRealizations = methodsRelation.getRelationsByRequirementId(storageRelations, elementObject.id);
            requirementRealizations.forEach(rel => modelRelation.setRelationValue(rel, 'startId', newValue));

        } else if (elementProp == 'type' && storageRelations != undefined) { // element value with relation impact
            methodsGeneral.setJSONObjectValueByName(elementObject, elementProp, newValue);

            var changedRelations = storageRelations.filter(rel => rel.endId == elementObject.id);
            changedRelations.forEach(rel => modelRelation.setRelationValue(rel, 'layer', newValue));

        } else { // element values
            methodsGeneral.setJSONObjectValueByName(elementObject, elementProp, newValue);
        };

        methodsGeneral.setJSONObjectValueByName(elementObject, 'lastChange', methodsGeneral.getCurrentDate());
    },

    setElementValueArray(elementObject, elementProp, newValue, method) {
        var newPropValue = [];
        var elementPropValue = methodsGeneral.getJSONObjectValueByName(elementObject, elementProp, 'Array');

        if (method == "add") {
            if (elementPropValue.indexOf(newValue) == -1) {
                elementPropValue.push(newValue);
                newPropValue = elementPropValue;
            }

        } else if (method == "remove") {
            newPropValue = elementPropValue.filter(val => val != newValue);
        };
        console.log(elementPropValue);
        this.setElementValue(elementObject, elementProp, newPropValue);
    },

    addElement(elementStorage, elementName, elementType, elementValues) { // return new element id
        var newElementId = getRandomElementId(elementStorage);
        var elementObject = {
            id: newElementId,
            name: elementName,
            type: elementType
        };

        var elementValueObjects = [];
        if (elementValues != undefined) {
            elementValueObjects = elementValues;
        }
        elementObject.values = elementValueObjects;
        methodsGeneral.setJSONObjectValueByName(elementObject, 'lastChange', methodsGeneral.getCurrentDate());

        elementStorage.push(elementObject);

        return newElementId;
    }
}


