<template>
    <b-row>
        <b-container class="m-2" fluid>
            <b-row class="mb-4">
                <b-col class="form-check form-check-inline">
                    <small class="form-text text-muted">
                        Interaktives Diagramm:
                        <p>
                            Einfach-<b-icon-mouse></b-icon-mouse>
                            :: Eingrenzung der Darstellung und Tabelle auf Selektion
                            (Mehrfachselektion möglich)
                        </p>
                    </small>
                </b-col>
            </b-row>
            <b-row class="w-75 mb-4">
                <basic-svg-bubble-packing
                    :circle-nodes="graphNodes"
                    :selected-keys="selectedNodeIds"
                    :consume-keys="selectedConsumeNodes"
                    :dependency-keys="selectedDependencyNodes"
                    :svgwidth="graphWidth"
                    :svgheight="graphHeight"
                    :padding-nodes="3"
                    :legend-name-fill="$relationTypes"
                    key-property="id"
                    display-property="name"
                    @nodeClick="changeSelectedElementIds" />
                <b-col sm>
                    <b-card title="Diagramm" class="p-3 mb-3">
                        <b-card-text>
                            <small>Gruppierung:</small>
                            <b-form-select v-model="selectedGrouping" :options="ddLayers"></b-form-select>
                        </b-card-text>
                        <b-card-text>
                            <small>Ebene:</small>
                            <b-form-select v-model="selectedNodeLayer" :options="ddLayers"></b-form-select>
                        </b-card-text>
                        <b-card-text>
                            Schriftgröße | View auf Selektion eingrenzen (keine Selektion >
                            wieder alle anzeigen)
                        </b-card-text>
                    <!-- TODO: Darstellung auf Selektion eingrenzen -->
                    </b-card>
                    <b-card title="Element" class="p-3 mb-3" v-if="selectedNodeIds.length == 1">
                        <b-card-text>
                            <b-button size="sm" variante="outline-info" @click="showElementDetails">Details / bearbeiten</b-button>
                        </b-card-text>
                        <b-card-text>
                            <small>Name | Upper-Element | Lower-Elements</small>
                            <!-- TODO: Show Up-Element + Down-Elemente -->
                            <!-- TODO: Schriftgröße ändern -->
                        </b-card-text>
                        <my-element-modal
                            :show="showElementModal"
                            :elementId="selectedNodeIds[0]"
                            :elements="elements"
                            :hierarchy="hierarchy"
                            @closeModal="hideElementDetails"
                            @changeElement="changeElement"
                            @changeHierarchy="changeHierarchy" />
                    </b-card>
                    <the-overview-relations
                        :consumeRelations="consumeRelations"
                        :dependencyRelations="dependencyRelations"
                        :show="selectedNodeIds.length > 0"
                        :elements="elements"
                    />
                </b-col>
            </b-row>
        </b-container>
    </b-row>
</template>

<script>
import BasicSvgBubblePacking from "./BasicSvgBubblePacking.vue";
import TheOverviewRelations from "./TheOverviewRelations.vue";
import MyElementModal from "./MyElementModal.vue";

export default {
  props: {
    consumeRelations: {
      type: Array,
      required: true,
      default: () => [],
    },
    dependencyRelations: {
      type: Array,
      required: true,
      default: () => [],
    },
    elements: {
      type: Array,
      required: true,
      default: () => [],
    },
    hierarchy: {
      type: Array,
      required: true,
      default: () => [],
    },
  },

  components: {
    BasicSvgBubblePacking,
    TheOverviewRelations,
    MyElementModal,
  },

  data() {
    return {
      graphWidth: 600,
      graphHeight: 600,
      selectedGrouping: "",
      selectedNodeLayer: "",
      selectedNodeIds: [],
      showElementModal: false,
      myUrl: "#",
      myfilename: "",
    };
  },

  created() {
    this.graphWidth = window.innerHeight - 150;
    this.graphHeight = window.innerHeight - 150;
    this.selectedGrouping = this.$viewLayers[0].name;
    this.selectedNodeLayer = this.$viewLayers[0].name;
  },

  computed: {
    // TODO: window resize

    ddLayers() {
        return this.$viewLayers.map(lay => lay.name);
    },

    graphNodes() {
        return this.generateSvgNodes();
    },

    selectedNestedIds() {
      var selectedIds = [];
      this.selectedNodeIds.forEach((id) => {
        selectedIds = selectedIds.concat(this.getNestedIds(id, 0), {
          value: id,
          depth: 0,
          parent: id,
        });
      });

      return selectedIds;
    },

    selectedConsumeNodes() {
      if (this.selectedNodeIds.length == 1) {
        var consumeElements = this.consumeRelations.map((rel) => {
          var startNode = this.selectedNestedIds[
            this.selectedNestedIds.map((n) => n.value).indexOf(rel.startId)
          ];
          return {
            value: rel.endId,
            depth: startNode.depth,
          };
        });

        return this.upScaleNodes(consumeElements);
      }

      return [];
    },

    selectedDependencyNodes() {
      var dependencyElements = this.dependencyRelations.map((rel) => {
        var endNode = this.selectedNestedIds[
          this.selectedNestedIds.map((n) => n.value).indexOf(rel.endId)
        ];
        return {
          value: rel.startId,
          depth: endNode != undefined ? endNode.depth : -1,
        };
      });

      return this.upScaleNodes(dependencyElements);
    },
  },

  watch: {
    selectedNestedIds(newVal) {
      this.$emit("selectionChanged", newVal, this.selectedNodeIds);
    },

    selectedGrouping() {
      this.selectedNodeIds = [];
    },

    selectedNodeLayer() {
      this.selectedNodeIds = [];
    },
  },

  methods: {
    emitDownloadRequirements() {
      this.$emit("autoDownloadRequirements");
    },

    emitLoadStorage() {
      this.$emit("loadStorage");
    },

    changeSelectedElementIds(nodeId) {
      if (this.selectedNodeIds.indexOf(nodeId) == -1) {
        this.selectedNodeIds.push(nodeId);
      } else {
        // click on selected nodeId to remove
        this.selectedNodeIds.splice(this.selectedNodeIds.indexOf(nodeId), 1);
      }
    },

    upScaleNodes(nodeArray) {
      // [id, depth]
      var scaleNodes = [];
      var nodeArrayCount = nodeArray.length;
      for (var e = 0; e < nodeArrayCount; e++) {
        var nodeElement = nodeArray[e];
        var currentDepth = nodeElement.depth;
        var currentId = nodeElement.value;
        var parentId = nodeElement.value;

        while (currentDepth > 0) {
          parentId = this.hierarchy.find((e) => e.subelemid == currentId);
          if (parentId != undefined) {
            currentId = parentId.elemid;
            currentDepth--;
          } else {
            currentDepth = 0;
          }
        }
        if (scaleNodes.indexOf(currentId) == -1) {
          scaleNodes.push(currentId);
        }
      }

      return scaleNodes;
    },

    getNestedIds(parentId, depthNodeId) {
      var childKeys = this.hierarchy.filter((e) => e.elemid == parentId);

      var childCounts = childKeys.length;
      var childsOfParent = [];
      if (childCounts > 0) {
        childsOfParent = childKeys.map((e) => ({
          value: e.subelemid,
          depth: depthNodeId + 1,
        }));
        for (var c = 0; c < childCounts; c++) {
          childsOfParent = childsOfParent.concat(
            this.getNestedIds(childKeys[c].subelemid, depthNodeId + 1)
          );
        }
      }

      if (depthNodeId == 0) {
        return childsOfParent.map(function (child) {
          child.parent = parentId;
          return child;
        });
      } else {
        return childsOfParent;
      }
    },

    getNestedLayerNodes(elemNodeId, targetType) {
      var targetNodes = this.hierarchy
        .filter((e) => e.elemid == elemNodeId)
        .map((e) => this.elements.find((elem) => elem.id == e.subelemid));
      var returnNodes = [];

      for (var ne = 0; ne < targetNodes.length; ne++) {
        if (targetNodes[ne].type == targetType) {
          returnNodes.push({
            id: targetNodes[ne].id,
            name: targetNodes[ne].name,
          });
        } else {
          returnNodes = returnNodes.concat(
            this.getNestedLayerNodes(targetNodes[ne].id, targetType)
          );
        }
      }

      return returnNodes;
    },

    generateSvgNodes() {
      if (this.elements == undefined) {
        return [];
      }

      var selectedLayer = this.selectedNodeLayer;
      var svgNodes = [];
      var nestedNodes;

      switch (this.selectedNodeLayer) {
        case this.$viewLayers[6].name: // Netzwerk (2-depth)
        case this.$viewLayers[5].name: // Applikation (2-depth)
        case this.$viewLayers[4].name: // Serviceinstanz (2-depth)
        case this.$viewLayers[3].name: // Solution (2-depth)
        case this.$viewLayers[2].name: // Release (2-depth)
        case this.$viewLayers[1].name: // Funktionalität (2-depth)
          var noProjectNode = {
            id: "ELMPRJ0",
            name: "No Project",
            parentNodeId: "ELM0",
          };

          var projectLayerElements = [];
          var addedProjects = [];
          this.elements
            .filter((e) => e.type == this.selectedGrouping)
            .forEach((elemProj) => {
              projectLayerElements = this.getNestedLayerNodes(
                elemProj.id,
                this.selectedNodeLayer
              );
              if (projectLayerElements.length > 0) {
                svgNodes = svgNodes.concat(
                  projectLayerElements.map(function (rel) {
                    return {
                      id: rel.id,
                      name: rel.name,
                      parentNodeId: elemProj.id,
                    };
                  })
                );
                addedProjects.push(elemProj.id);
              }
            });

          addedProjects.forEach((elemProjId) => {
            var projName = this.elements.find((e) => e.id == elemProjId).name;
            svgNodes.push({
              id: elemProjId,
              name: projName,
              parentNodeId: "ELM0",
            });
          });

          var noProjectLayerElements = this.elements
            .filter((e) => e.type == this.selectedNodeLayer)
            .filter(function (elemRel) {
              return svgNodes.map((e) => e.id).indexOf(elemRel.id) == -1;
            });

          svgNodes = svgNodes.concat(
            noProjectLayerElements.map(function (elemRel) {
              return {
                id: elemRel.id,
                name: elemRel.name,
                parentNodeId: "ELMPRJ0",
              };
            })
          );

          svgNodes.push(noProjectNode);
          break;

        default:
        case this.$viewLayers[0].name: // Projekte (1-depth)
          svgNodes = this.elements
            .filter((e) => e.type == this.$viewLayers[0].name)
            .map(function (elemNode) {
              return {
                id: elemNode.id,
                name: elemNode.name,
                parentNodeId: "ELM0",
              };
            });
          break;
      }

      svgNodes = svgNodes.filter(
        (elem, ind, _arr) => _arr.map((e) => e.id).indexOf(elem.id) === ind
      );
      svgNodes.push({
        id: "ELM0",
        name: "",
        parentNodeId: "",
      });

      return d3
        .stratify()
        .id((n) => n.id)
        .parentId((n) => n.parentNodeId)(svgNodes);
    },

    changeElement(changeObject) {
      this.$emit("changeElement", changeObject);
    },

    changeHierarchy(changeObject) {
      this.$emit("changeHierarchy", changeObject);
    },

    showElementDetails() {
      this.showElementModal = true;
    },

    hideElementDetails() {
      this.showElementModal = false;
    },
  },
};
</script>

<style scoped>
</style>



